.passcode-page {
  font-family: 'Montserrat', sans-serif; }
  .passcode-page p {
    font-weight: bold;
    text-align: center; }
  .passcode-page .time {
    margin-top: 60px;
    color: #ea5535;
    font-size: 20px;
    margin-top: 40px; }
  .passcode-page .title {
    font-size: 24px;
    margin: 15px 0 20px;
    color: #363636; }
  .passcode-page .name {
    color: #008eff;
    font-size: 20px;
    margin-top: 30px;
    margin-bottom: 0;
    display: flex; }
    .passcode-page .name span {
      flex: 1;
      text-align: center; }
  .passcode-page .tip {
    color: #343434;
    margin-top: 10px;
    font-size: 21px;
    display: flex;
    justify-content: center;
    margin-bottom: 20px; }
    .passcode-page .tip .tip-box p {
      margin: 0;
      text-align: left; }
  .passcode-page .back-btn {
    color: #fff;
    width: 240px;
    padding: 8px 24px;
    font-size: 17px;
    text-align: center;
    margin: 0 auto;
    border-radius: 4px;
    background-color: #07c160; }
  .passcode-page img {
    width: 60%;
    display: block;
    margin: 0 auto; }
